* {
    margin: 0;
    padding: 0;
    -webkit-text-size-adjust: none;
}

*,
*::before,
*::after {
    -webkit-moz-box-sizing: border-box;
    -ms-moz-box-sizing: border-box;
    -o-moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
font,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
    font-weight: normal;
}

sub {
    vertical-align: sub;
    font-size: smaller;
}

sup {
    vertical-align: super;
    font-size: smaller;
}

dfn {
    font-style: normal;
}

picture {
    display: block;
}

video::-internal-media-controls-download-button {
    display: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
}

main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

nav,
ul,
ol,
dl,
li {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after {
    content: '';
    content: none;
}

q:before,
q:after {
    content: '';
    content: none;
}

a {
    margin: 0;
    padding: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
}

ins {
    background-color: #ff9;
    color: #000;
    text-decoration: none;
}

mark {
    background-color: #ff9;
    color: #000;
    font-style: italic;
    font-weight: bold;
}

del {
    text-decoration: line-through;
}

abbr[title],
dfn[title] {
    border-bottom: 1px dotted;
    cursor: help;
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #cccccc;
    margin: 1em 0;
    padding: 0;
}

legend,
caption,
figcaption {
    display: none;
    clear: both;
}

label {
    cursor: auto;
}

button {
    margin: 0;
    color: inherit;
    vertical-align: middle;
    cursor: pointer;
    border: none;
    background-color: transparent;
    font-weight: inherit;
}

input {
    margin: 0;
    font: inherit;
    color: inherit;
    vertical-align: middle;
}

a:focus,
button:focus,
[tabindex]:focus,
input:focus {
    outline: #fff dashed 1px;
}

@supports (overflow: overlay) {
    a:focus,
    button:focus,
    [tabindex]:focus,
    input:focus {
        outline: transparent;
    }
    a:focus-visible,
    button:focus-visible,
    [tabindex]:focus-visible,
    input:focus-visible {
        outline: #fff dashed 1px;
        -webkit-box-shadow: inset 0 0 3px #000;
        box-shadow: inset 0 0 3px #000;
        outline-offset: -2px;
        -webkit-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        z-index: 10;
    }
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0;
}

* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    outline-color: transparent;
}

::-webkit-input-placeholder {
    padding-top: 3px;
    color: #cbcbcb;
}

input[type='text'],
input[type='password'],
input[type='tel'],
input[type='email'],
input[type='date'],
input[type='search'],
input[type='number'] {
    width: 100%;
}

*::-webkit-search-cancel-button {
    -webkit-appearance: none;
}

input[type='reset'],
input[type='email'],
input[type='number'],
input[type='search'],
input[type='date'],
input[type='text'],
input[type='password'],
select {
    cursor: pointer;
    border: 1px solid #ddd;
    -webkit-border-radius: 0;
    -webkit-appearance: none;
    padding: 3px 5px;
}

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
    margin: 0;
}

button {
    -webkit-appearance: button;
}

input[type='checkbox'],
input[type='radio'] {
    width: 20px;
    height: 20px;
    padding: 0;
    -webkit-border-radius: 0;
    -webkit-appearance: none;
}

input::-ms-check {
    display: none;
}

textarea {
    margin: 0;
    resize: none;
    font: inherit;
    color: inherit;
    -webkit-border-radius: 1px;
    -webkit-appearance: none;
}

select {
    position: relative;
    margin: 0;
    font: inherit;
    color: inherit;
    vertical-align: middle;
    width: 100%;
    height: 25px;
    background-repeat: no-repeat;
    background-position: 98% 10px;
    background-color: #fff;
    -webkit-border-radius: 0;
    -moz-appearance: none;
    background-size: 8px 4px;
}

optgroup {
    font: inherit;
    color: inherit;
    margin: 0;
}

img {
    max-width: 100%;
    vertical-align: top;
}

a {
    color: inherit;
}

a:link,
a:visited,
a:active {
    text-decoration: none;
}

@media (hover: hover) {
    a:hover {
        text-decoration: none;
    }
}

address,
em {
    font-style: normal;
}

*::-moz-selection {
    background-color: #000;
    color: #fff;
}

*::selection {
    background-color: #000;
    color: #fff;
}

*::-moz-selection {
    background-color: #000;
    color: #fff;
}

*::-webkit-selection {
    background-color: #000;
    color: #fff;
}

